import {configureStore, createSlice} from "@reduxjs/toolkit";

let user = createSlice({
    name: 'user', // state 
    initialState: {name: 'Lee', age: 20}, // state  
    reducers: {
        changeName(state) {
            state.name = 'park' // Lee  
        },
    }
})

let stock = createSlice({
    name: 'stock',
    initialState: [7,8,9]
})

let cart = createSlice({
    name: 'cart',
    initialState: [
        {
            id : 1,
            title : "   ",
            content : ".  ,  6,  ,   .",
            count: 2,
            price : 5575000,
        },

        {
            id : 2,
            title : "    ",
            content : ".  ,  6,   ,  ,   .",
            count: 1,
            price : 5145000,
        }
    ]
})

export default configureStore({
    reducer:{
        user: user.reducer,
        cart: cart.reducer,
        stock: stock.reducer
    }
})

export let {changeName} = user.actions